import React, { useState, Fragment, useEffect } from 'react'
import clsx from 'clsx'
import { Route, Link, BrowserRouter, Switch } from 'react-router-dom'
import { createBrowserHistory } from 'history'

import { withStyles } from '@material-ui/core/styles'
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import Typography from '@material-ui/core/Typography'
import Drawer from '@material-ui/core/Drawer'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import IconButton from '@material-ui/core/IconButton'
import MenuIcon from '@material-ui/icons/Menu'

import Home from '../pages/Home'
import API from '../pages/API'
import About from '../pages/About'

import headerStyle from '../styles/header.style'
import {
  BookOutlined,
  CloseOutlined,
  HistoryOutlined,
  HomeOutlined,
  InfoOutlined,
  NightsStay,
  WbSunny
} from '@material-ui/icons'
import { Divider, Snackbar, Tooltip } from '@material-ui/core'
import PageNotFound from '../pages/PageNotFound'
import Alert from './Alert'
import History from '../pages/History'

const history = createBrowserHistory()

const HeaderToolbar = withStyles(headerStyle)(
  ({ classes, title, onMenuClick }) => {
    const [updateTime, setUpdateTime] = useState(
      `${new Date().toLocaleTimeString('ko-KR', {
        hour12: true,
        hour: 'numeric',
        minute: 'numeric'
      })}`
    )
    const [darkModeButtonClicked, setDarkModeButtonClicked] = useState(false)

    const handleDarkModeSnackbarClose = (event, reason) => {
      if (reason === 'clickaway') {
        return
      }
      setDarkModeButtonClicked(false)
    }

    const refreshRate = 60000 // 1ë¶„ë§ˆë‹¤ ì—…ë°ì´íŠ¸

    const updateDataTime = async () => {
      setUpdateTime(
        `${new Date().toLocaleTimeString('ko-KR', {
          hour12: true,
          hour: 'numeric',
          minute: 'numeric'
        })}`
      )
    }

    const toggleDarkMode = () => {
      setDarkModeButtonClicked(true)
      if (localStorage.getItem('darkmode') === 'true') {
        localStorage.setItem('darkmode', false)
      } else {
        localStorage.setItem('darkmode', true)
      }
      window.location.reload()
    }

    useEffect(() => {
      const interval = setInterval(updateDataTime, refreshRate)
      return () => clearInterval(interval)
    }, [refreshRate])

    return (
      <Fragment>
        <AppBar className={classes.aboveDrawer}>
          <Toolbar>
            <Tooltip title='ë©”ë‰´' arrow>
              <IconButton
                className={classes.menuButton}
                color='inherit'
                aria-label='Menu'
                onClick={onMenuClick}>
                <MenuIcon />
              </IconButton>
            </Tooltip>
            <Typography variant='h6' color='inherit' className={classes.flex}>
              {title}
            </Typography>
            <Typography variant='subtitle2' color='inherit'>
              {`${updateTime}`}ì— ì—…ë°ì´íŠ¸ ë¨
            </Typography>
            <div>
              <IconButton
                aria-label='account of current user'
                aria-controls='menu-appbar'
                aria-haspopup='true'
                onClick={toggleDarkMode}
                color='inherit'>
                {localStorage.getItem('darkmode') === 'true' ? (
                  <WbSunny />
                ) : (
                  <NightsStay />
                )}
              </IconButton>
            </div>
          </Toolbar>
        </AppBar>
        <div className={classes.toolbarMargin} />
        <Snackbar
          open={darkModeButtonClicked}
          autoHideDuration={6000}
          onClose={handleDarkModeSnackbarClose}>
          <Alert onClose={handleDarkModeSnackbarClose} severity='success'>
            í…Œë§ˆ ë³€ê²½ ë° ì ìš©ì¤‘...
          </Alert>
        </Snackbar>
      </Fragment>
    )
  }
)

const HeaderDrawer = withStyles(headerStyle)(
  ({
    classes,
    variant,
    open,
    onClose,
    onItemClick,
    onItemClickWithoutUpdate
  }) => (
    <BrowserRouter history={history}>
      <Drawer
        variant={variant}
        open={open}
        onClose={onClose}
        classes={{
          paper: classes.drawerPaper
        }}>
        <div
          className={clsx({
            [classes.toolbarMargin]: variant === 'persistent'
          })}
        />
        <List>
          <ListItem button component={Link} to='/' onClick={onItemClick('í™ˆ')}>
            <ListItemIcon>
              <HomeOutlined />
            </ListItemIcon>
            <ListItemText>í™ˆ</ListItemText>
          </ListItem>
          <ListItem
            button
            component={Link}
            to='/history'
            onClick={onItemClick('ê¸°ë¡')}>
            <ListItemIcon>
              <HistoryOutlined />
            </ListItemIcon>
            <ListItemText>ê¸°ë¡</ListItemText>
          </ListItem>
          <Divider />
          <ListItem
            button
            component={Link}
            to='/api'
            onClick={onItemClick('API ë¬¸ì„œ')}>
            <ListItemIcon>
              <BookOutlined />
            </ListItemIcon>
            <ListItemText>API ë¬¸ì„œ</ListItemText>
          </ListItem>
          <Divider />
          <ListItem
            button
            component={Link}
            to='/about'
            onClick={onItemClick('ì •ë³´')}>
            <ListItemIcon>
              <InfoOutlined />
            </ListItemIcon>
            <ListItemText>ì •ë³´</ListItemText>
          </ListItem>
          <Divider />
          <ListItem button onClick={onItemClickWithoutUpdate()}>
            <ListItemIcon>
              <CloseOutlined />
            </ListItemIcon>
            <ListItemText>ë©”ë‰´ ë‹«ê¸°</ListItemText>
          </ListItem>
        </List>
      </Drawer>
      <main className={classes.content}>
        <Switch>
          <Route exact path='/' component={Home} />
          <Route path='/api' component={API} />
          <Route path='/about' component={About} />
          <Route path='/history' component={History} />
          <Route component={PageNotFound} />
        </Switch>
      </main>
    </BrowserRouter>
  )
)

const HeaderAppBarInteraction = ({ classes, variant }) => {
  const [drawer, setDrawer] = useState(false)
  const [title, setTitle] = useState()

  useEffect(() => {
    if (window.location.pathname === '/') {
      setTitle('í™ˆ')
    } else if (window.location.pathname === '/api') {
      setTitle('API ë¬¸ì„œ')
    } else if (window.location.pathname === '/history') {
      setTitle('ê¸°ë¡')
    } else if (window.location.pathname === '/about') {
      setTitle('ì •ë³´')
    } else {
      setTitle('404')
    }
  }, [])

  const toggleDrawer = () => {
    setDrawer(!drawer)
  }

  const onItemClick = (title) => () => {
    setTitle(title)
    setDrawer(variant === 'temporary' ? false : drawer)
    setDrawer(!drawer)
  }

  const onItemClickWithoutUpdate = () => () => {
    setDrawer(variant === 'temporary' ? false : drawer)
    setDrawer(!drawer)
  }

  return (
    <div className={classes.root}>
      <HeaderToolbar title={title} onMenuClick={toggleDrawer} />
      <HeaderDrawer
        open={drawer}
        onClose={toggleDrawer}
        onItemClick={onItemClick}
        onItemClickWithoutUpdate={onItemClickWithoutUpdate}
        variant={variant}
      />
    </div>
  )
}

export default withStyles(headerStyle)(HeaderAppBarInteraction)
